import axios from 'axios';
import React from 'react'
import { useState } from 'react'
import { Link } from 'react-router-dom';

function Chapter({ props }) {
    
    const [seen, setSeen] = useState(!!(props?.accountData?.db?.chapters?.find?.(x => x.chapterNum === props?.ch?.moreData?.db?.chapterNum)?.seen));
    function changeSeen() {
        axios({
            url: `${props.backEnd}/account/mark`,
            data: {
                accessToken: localStorage.getItem('accessToken'),
                mangaUrl: props?.ch?.moreData?.formatedRoutes?.chapterArUrl,
                mangaData: {...props.mangaData, chapters: []},
                seen: !seen
            },
            method: "POST"
        }).then(res => {
            setSeen(res.data.seen);
        })

    }
    return (
        <div className = " transition duration-500 border-sec hover:bg-black/60 border-2 mx-4 flex flex-col items-end px-2 py-1 rounded">
            <Link to={props.ch.moreData.formatedRoutes.mangaSekaichapterArUrl}>
                <h1 className = "text-lg text-white text-right">{props.ch.num}</h1>
                <h1 className = "text-md text-gray-300 text-right -mt-0.5">{props.ch.date}</h1>
            </Link>
            <button onClick={changeSeen}>
                {seen ? <svg xmlns="http://www.w3.org/2000/svg" className="mt-2 h-6 w-6 text-sec" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z" />
                </svg> : <svg xmlns="http://www.w3.org/2000/svg" className="mt-2 h-6 w-6 text-gray-100 flex flex-row items-end" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z" />
                </svg>}
            </button>
            
            
        </div>
    )
}

export default Chapter
