import React, { useRef , useEffect } from 'react'
import axios from 'axios';
import Sidebar from "./Sidebar";
import { Link } from 'react-router-dom';


function Navbar({props}) {
    const [isOpen, setOpen] = React.useState(false);
    let accessToken = localStorage.getItem('accessToken');
    const [user, setUser] = React.useState({
        username: "",
        image: "",
        role: "",
        id: ""
    });

    useEffect(() => {
        if (props.backEnd) {
            axios({
                url: `${props.backEnd}/account/user`,
                method:'POST',
                data: { accessToken: accessToken }
            }).then(res => {

                if (res.data.success) {
                    setUser(res.data)
                }
            })
        }
        
    }, [accessToken, props.backEnd])

    const searchRef = useRef()
    function handelSearch(event) {
        props.handelSearchValue(searchRef)
    }
    if (props.search) return (
        <>
        <Sidebar props= {{ user: user, isOpen, handelClose: () => setOpen(false)}} />
        <div className ="fixed z-30 w-full bg-thr top-0">
            <div className = "mx-2.5 py-2 flex justify-between items-center">
                <div className = "flex items-center space-x-4">
                    <button onClick = {() => setOpen(!isOpen)}>
                        <svg xmlns="http://www.w3.org/2000/svg" class={`h-8 w-8 text-gray-100 transition transform duration-500 hover:-rotate-6 ${isOpen ? '-rotate-6' : ''}`} fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 6h16M4 12h16M4 18h16" />
                        </svg>
                    </button>
                    <div className ="flex items-center space-x-2">
                        <form name="searchBox" onSubmit={e => { e.preventDefault(); handelSearch(e) }} >
                            <div className = "relative">
                            <input ref = {searchRef} type = 'text' placeholder= "Search Manga Sekai" className = "transition duration-200 rounded pl-8 bg-four h-7 placeholder-current text-gray-300 focus:outline-none focus:ring focus:ring-sec"></input>
                                <button type = 'submit'>
                                    <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 text-gray-300 absolute top-1 left-1.5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                                    </svg>
                                </button>
                            </div>
                        </form>
                        

                    </div>
                </div>

                </div>
        </div>
        </>
    )
    return (
        <div className ="fixed z-30 w-full bg-thr top-0">
            <Sidebar props= {{user: user, isOpen, handelClose: () => setOpen(false)}} />

            <div className = "mx-2.5 py-2 flex justify-between items-center">
                <div className = "flex items-center space-x-4">
                <button onClick = {() => setOpen(!isOpen)}>
                    <svg xmlns="http://www.w3.org/2000/svg" className={`h-8 w-8 text-gray-100 transition transform duration-500 hover:-rotate-6 ${isOpen ? '-rotate-6' : ''}`} fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 6h16M4 12h16M4 18h16" />
                    </svg>
                </button>
                    <div className = "flex flex-col ">
                        {props.titleHref ?  <Link to = {props.titleHref} className = "text-white text-2xl -mt-0.5">{props.title}</Link> : <h1 className = "text-white text-2xl -mt-0.5">{props.title}</h1>}
                        {props.secTitle && <h1 className = "text-gray-200 text-lg -mt-0.5">{props.secTitle}</h1>}
                    </div>
                    
                </div>
                <Link to = "/search">
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-7 w-7 text-gray-100" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                    </svg>
                </Link>
            </div>
        </div>
    )
}

export default Navbar
