import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";


import Latest from './pages/Latest'
import LastWatched from './pages/LastWatched'

import Search from './pages/Search'
import Manga from './pages/Manga'
import Login from './pages/Login'
import Profile from './pages/Profile'
import Mylist from './pages/Mylist'
import MangaGList from './pages/MangaGList'

import Settings from "./pages/Settings"


import axios from 'axios'
import { useEffect, useState } from "react";
import ProfileList from "./pages/ProfileList";
import Downloads from "./pages/Downloads";

import OMangaReader from "./pages/MangaReader";
import NMangaReader from "./pages/NMangaReader";

const dev = false;



function App() {
  const [servers, setServers] = useState({})


  useEffect(() => {
    if (dev) return setServers({ backEnd: 'http://localhost:3001' });
    axios('https://www.manga-sekai.tk/apiServers').then(res => {
        if (!dev) setServers({ backEnd: res.data.backendApi });
    });
  }, []);
  var currentDark = localStorage.getItem('darkmode') || "sdark";
  var currentColor = localStorage.getItem('color') || 'main';
  var currentReader = localStorage.getItem('reader') || 'old';

  document.body.classList.add(currentColor);
  document.body.classList.add(currentDark);

  
  return (
    servers.backEnd ? 
    <Router>
        <Switch>
          <Route exact path="/">
            <Latest props ={servers}/>
          </Route>
          <Route exact path="/lastWatched">
            <LastWatched props ={servers}/>
          </Route>
          <Route exact path="/search">
            <Search props ={servers}/>
          </Route>
          <Route exact path="/mylist">
            <Mylist props ={servers}/>
          </Route>
          <Route exact path="/profile/:id">
            <Profile props ={servers}/>
          </Route>
          <Route exact path="/profile/:id/list">
            <ProfileList props ={servers}/>
          </Route>
          <Route exact path="/manga/:mangaid">
            <Manga props ={servers}/>
          </Route>
          <Route exact path="/manga/:mangaid/:chapterid">
            {currentReader == 'old' ? <OMangaReader props ={servers}/> : <NMangaReader props ={servers}/>}
          </Route>
          <Route path="/login">
            <Login props ={servers}/>
          </Route>
          <Route path="/settings">
            <Settings props ={servers}/>
          </Route>
          <Route path="/mangalist">
            <MangaGList props ={servers}/>
          </Route>
          <Route path="/downloads">
            <Downloads props ={servers}/>
          </Route>
          
          
          

        </Switch>
    </Router> : null
    
  );
}

export default App;
