import React from 'react'
import ImageLod from './ImageLod.js'
//h-[215px] md:h-[245px] lg:h-[270px] xl:h-[305px]
function Mangacard({ props }) {
    return (
        <div className = " transition bg-thr grid grid-cols-1 grid-rows-[min-content_auto] overflow-hidden group hover:opacity-80 hover:-translate-y-1">

            <div className = "relative">
                <ImageLod as="img" className = 'h-[190px] md:h-[220px] lg:h-[245px] xl:h-[280px] object-cover object-center w-full' props={{src: props?.manga?.image}} src={props?.manga?.image} loading="lazy" alt="" />
                {props?.manga?.latestCh ? <div className = "animate-fadeinslow absolute inline-block bottom-2 left-2 bg-sec rounded-md py-[1px] px-[5px]">
                        <h1 className = 'text-gray-100 text-sm'>{props?.manga?.latestCh?.num}</h1>
                    </div> : ""}
                    {props?.manga?.year ? <div className = "animate-fadeinslow absolute inline-block bottom-2 left-2 bg-sec rounded-md py-[1px] px-[5px]">
                        <h1 className = 'text-gray-100 text-sm'>{props?.manga?.year}</h1>
                    </div> : ""}
                    {props?.manga?.dateAdded ? <div className = "animate-fadeinslow absolute inline-block bottom-2 left-2 bg-sec rounded-md py-[1px] px-[5px]">
                        <h1 className = {`text-gray-100 ${props?.manga?.dateAdded?.length > "9 minutes ago"?.length ? "text-[0.69rem]": "text-xs"}`}>{props?.manga?.dateAdded}</h1>
                    </div> : ""}

                    {props.manga?.moreInfo?.flag ?
                        <img className = "animate-fadeinslow absolute w-auto h-[25px] rounded left-1 top-2 shadow-2xl" src = {props.manga?.moreInfo?.flag} alt ={props?.manga?.moreInfo?.countryOfOrigin}/>
                    : ""}
            </div>

            <div className = "py-1 pl-1 px-0.5">

                <h1 className = " self-start text-center text-gray-100 text-sm md:text-base whitespace-nowrap overflow-hidden text-ellipsis my-0.5">{props?.manga?.name}</h1>

                <div className = "flex justify-center">

                    {props?.manga?.rating ? <h1 className = "mt-0.5 mx-0.5 text-sm text-gray-200 flex items-center text-left">
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 text-yellow-400 mx-1" viewBox="0 0 20 20" fill="currentColor">
                        <path fillRule="evenodd" d="M5 2a1 1 0 011 1v1h1a1 1 0 010 2H6v1a1 1 0 01-2 0V6H3a1 1 0 010-2h1V3a1 1 0 011-1zm0 10a1 1 0 011 1v1h1a1 1 0 110 2H6v1a1 1 0 11-2 0v-1H3a1 1 0 110-2h1v-1a1 1 0 011-1zM12 2a1 1 0 01.967.744L14.146 7.2 17.5 9.134a1 1 0 010 1.732l-3.354 1.935-1.18 4.455a1 1 0 01-1.933 0L9.854 12.8 6.5 10.866a1 1 0 010-1.732l3.354-1.935 1.18-4.455A1 1 0 0112 2z" clipRule="evenodd" />
                    </svg>
                    {props?.manga?.rating?.score}</h1> : ""}
                </div>

            </div>
  


        </div>
    )
}

export default Mangacard
