import React, { useEffect, useState } from 'react'
import Navbar from '../components/Navbar'
import axios from 'axios';
import Loader from "react-loader-spinner";
import { useHistory } from 'react-router-dom';
import { useRef } from 'react';

function Settings(inputs) {
    let { props } = inputs;
    let history = useHistory();
    function Logout() {
        localStorage.setItem("accessToken", "");
        history.push('/');
        window.location.reload();
    }
    var [themeOpen, setThemeOpen] = useState(false);
    var [accountOpen, setAccountOpen] = useState(false);
    var [readerOpen, setReaderOpen] = useState(false);

    
    var bioRef = useRef();
    var [bioLoading, setBioLoading] = useState(false);
    var [user, setUser] = useState({ });
    useEffect(() => {
        if (props.backEnd) {
            axios({
                url: `${props.backEnd}/account/user`,
                method:'POST',
                data: { accessToken: localStorage.getItem('accessToken') }
            }).then(res => {
                setUser(res.data);
            })
        }
    }, [props.backEnd])
    
    var currentDark = localStorage.getItem('darkmode') || 'dark';
    var currentColor = localStorage.getItem('color') || 'main';
    var currentReader = localStorage.getItem('reader') || 'old';

    function SetDark(mode) {
        localStorage.setItem("darkmode", mode);
        window.location.reload();
    }
    
    function SetColor(color) {
        localStorage.setItem("color", color);
        window.location.reload();
    }

    function SetReader(reader) {
        localStorage.setItem("reader", reader);
        window.location.reload();
    }

    function handelBio(event) {
        let newBio = bioRef?.current?.value;
        setBioLoading(true);
        axios({
            url:props.backEnd+'/account/editUser',
            method: "POST",
            data: {
                accessToken: localStorage.getItem('accessToken'),
                bio: newBio,
            }
        }).then(res => {
            setUser({...user, aboutUser: newBio})
            setBioLoading(false);
        })
    }


    return (
        <div className ="animate-fadein">
            <Navbar props={ {title: 'Settings', backEnd: props.backEnd}} />
            <div className ="mt-14 flex flex-col justify-between h-[60vh]">
                <div className = 'flex flex-col items-start mx-2.5'>
                    <div className = "flex w-full flex-col space-y-4">

                        {/* Account */}
                        <div className={`w-full flex flex-col`}>
                            <button disabled={(localStorage.getItem("accessToken") || '')?.length < 1} onClick= {() => setAccountOpen(!accountOpen)} className = "w-full bg-sec text-center text-xl text-white flex flex-row justify-between items-center px-2 py-1.5 disabled:opacity-50">
                                <span></span>
                                <span>Account</span>
                                <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 " fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                    <path strokeLinecap="round" className = {`transition duration-300 ${accountOpen ? 'opacity-0' : 'opacity-100'}`} strokeLinejoin="round" strokeWidth="2" d="M19 9l-7 7-7-7" />
                                    <path strokeLinecap="round" className = {`transition duration-300 ${accountOpen ? 'opacity-100' : 'opacity-0'}`} strokeLinejoin="round" strokeWidth="2" d="M5 15l7-7 7 7" />
                                </svg>
                            </button>
                            <div className = {` transition-all  ease-in-out bg-sec-80 text-center text-xl text-white  duration-200 ${accountOpen ? `max-h-[400px] duration-700` :`max-h-[0px] duration-300`}  overflow-hidden`}>
                                <div className = "flex flex-col items-start px-2 py-1.5  space-y-2">
                                    <form className = "relative w-full" name="bioBox" onSubmit={e => { e.preventDefault(); handelBio(e) }} >
                                        <div className = "flex flex-col items-start w-full">
                                            <span className = "text-left text-sm my-0.5 ml-0.5">About Me :</span>
                                            {bioLoading ? 
                                                <Loader
                                                    as='div'
                                                    className= "flex flex-col items-center"
                                                    type="TailSpin"
                                                    color="#DCDCDC"
                                                    height={50}
                                                    width={50}
                                                />
                                            : <> <textarea defaultValue = {user?.aboutUser} maxLength="100" ref = {bioRef} type = 'text' placeholder= "About Me" className = "transition duration-200 rounded pl-2 bg-main h-32 w-full placeholder-current text-gray-300 focus:outline-none focus:ring focus:ring-sec">
                                            </textarea>
                                            <button type = 'submit' className = ' absolute bottom-2 right-2 text-green-500'>
                                                <svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-7" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M5 13l4 4L19 7" />
                                                </svg>
                                            </button></>}
                                            
                                        </div>
                                    </form>
                                    <button onClick= {Logout} className = {`flex items-center disabled:opacity-50`} disabled={(localStorage.getItem("accessToken") || '')?.length < 1}>
                                        <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 text-white mr-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M17 16l4-4m0 0l-4-4m4 4H7m6 4v1a3 3 0 01-3 3H6a3 3 0 01-3-3V7a3 3 0 013-3h4a3 3 0 013 3v1" />
                                        </svg>
                                        <span>Logout</span>
                                    </button>
                                </div>
                            </div>
                        </div>
                        

                        <div className={`w-full flex flex-col`}>
                            <button onClick= {() => setThemeOpen(!themeOpen)} className = "bg-sec text-center text-xl text-white flex flex-row justify-between items-center px-2 py-1.5">
                                <span></span>
                                <span>Theme</span>
                                <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 " fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                    <path strokeLinecap="round" className = {`transition duration-300 ${themeOpen ? 'opacity-0' : 'opacity-100'}`} strokeLinejoin="round" strokeWidth="2" d="M19 9l-7 7-7-7" />
                                    <path strokeLinecap="round" className = {`transition duration-300 ${themeOpen ? 'opacity-100' : 'opacity-0'}`} strokeLinejoin="round" strokeWidth="2" d="M5 15l7-7 7 7" />
                                </svg>
                            </button>
                            <div className = {` transition-all  ease-in-out bg-sec-80 text-center text-xl text-white  duration-200 ${themeOpen ? `max-h-[400px] duration-700` :`max-h-[0px] duration-300`}  overflow-hidden`}>
                                <div className = "flex flex-col items-start px-2 py-1.5 ">

                                    <div className = "flex flex-row items-center justify-center w-full py-1.5 space-x-2">
                                        <button onClick={() => SetDark("sdark")} className={`transition duration-200 active:scale-110 m-1 py-1 px-2 rounded border border-sec ${currentDark === "sdark" ? `bg-sec scale-110` : ``}  whitespace-nowrap`}><span>Dark</span></button>
                                        <button onClick={() => SetDark("adark")} className={`transition duration-200 active:scale-110 m-1 py-1 px-2 rounded border border-sec ${currentDark === "adark" ? `bg-sec scale-110` : ``}  whitespace-nowrap`}><span>Amoled Dark</span></button>

                                    </div>
                                    <div className = {`flex flex-row items-center justify-center w-full`}>
                                        <h1 className= {`text-center`}>Colors</h1>
                                    </div>
                                    <div className = "flex flex-row items-center justify-center w-full py-1.5 space-x-2">

                                        <button onClick={() => SetColor("main")} className={`transition duration-200 active:scale-110 m-1 py-1 px-2 rounded border border-sec ${currentColor === "main" ? `bg-sec scale-110` : ``}  whitespace-nowrap`}>
                                            <span>Red</span>
                                        </button>
                                        <button onClick={() => SetColor("blue")} className={`transition duration-200 active:scale-110 m-1 py-1 px-2 rounded border border-sec ${currentColor === "blue" ? `bg-sec scale-110` : ``}  whitespace-nowrap`}>
                                            <span>Blue</span>
                                        </button>
                                        <button onClick={() => SetColor("indigo")} className={`transition duration-200 active:scale-110 m-1 py-1 px-2 rounded border border-sec ${currentColor === "indigo" ? `bg-sec scale-110` : ``}  whitespace-nowrap`}>
                                            <span>Indigo</span>
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </div>

                        
                        
                        <div className={`w-full flex flex-col`}>
                            <button onClick= {() => setReaderOpen(!readerOpen)} className = "bg-sec text-center text-xl text-white flex flex-row justify-between items-center px-2 py-1.5">
                                <span></span>
                                <span>Reader</span>
                                <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 " fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                    <path strokeLinecap="round" className = {`transition duration-300 ${readerOpen ? 'opacity-0' : 'opacity-100'}`} strokeLinejoin="round" strokeWidth="2" d="M19 9l-7 7-7-7" />
                                    <path strokeLinecap="round" className = {`transition duration-300 ${readerOpen ? 'opacity-100' : 'opacity-0'}`} strokeLinejoin="round" strokeWidth="2" d="M5 15l7-7 7 7" />
                                </svg>
                            </button>
                            <div className = {` transition-all  ease-in-out bg-sec-80 text-center text-xl text-white  duration-200 ${readerOpen ? `max-h-[400px] duration-700` :`max-h-[0px] duration-300`}  overflow-hidden`}>
                                <div className = "flex flex-col items-start px-2 py-1.5 ">

                                    <div className = "flex flex-row items-center justify-center w-full py-1.5 space-x-2">
                                        <button onClick={() => SetReader("old")} className={`transition duration-200 active:scale-110 m-1 py-1 px-2 rounded border border-sec ${currentReader === "old" ? `bg-sec scale-110` : ``}  whitespace-nowrap`}><span>OLD Reader</span></button>
                                        <button onClick={() => SetReader("new")} className={`transition duration-200 active:scale-110 m-1 py-1 px-2 rounded border border-sec ${currentReader === "new" ? `bg-sec scale-110` : ``}  whitespace-nowrap`}><span>NEW Reader</span></button>
                                    </div>
                    
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Settings
