import { useHistory, useParams } from 'react-router';
import React, { useEffect, useState, useRef, useCallback } from 'react'
import Navbar from '../components/Navbar'
import axios from 'axios';
import Loader from "react-loader-spinner";
import MangaReaderFooter from '../components/MangaReaderFooter'
import ImageLoader from '../components/ImageLoader'



function MangaReader({props}) {
    let { mangaid, chapterid } = useParams()
    const [isOpen, setOpen] = useState(true);
    const [loading, setLoading] = useState(true);
    const [cloading, setCLoading] = useState(false);

    const [error, setError] = useState();
    const [data, setData] = useState({});
    const [hasMore, setMore] = useState(true);
    const [isSeen, setSeen] = useState(false);
    const observer = useRef();
    let history = useHistory();

    const lastElementRef = useCallback(node => {
      if (observer.current) observer.current.disconnect()
      observer.current = new IntersectionObserver(entries => {
        if (entries[0].isIntersecting) {
            if (hasMore) fetchData();
        }
      })
      if (node) observer.current.observe(node)

       
    }, [hasMore, fetchData])

    function fetchData () {
        setCLoading(true);
        axios({
            url: props.backEnd+'/manga/chapter',
            method:'POST',
            data: {url : data?.next?.formatedRoutes?.mangaSekaichapterArUrl}
        }).then(res => {
            if (res.data.chapter) {
                let conImages = [...data.images.concat(res.data.images), `end=${res?.data?.chapterName},${res?.data?.next?.arChapter}`];
                setData({...res.data, images: conImages});
                setCLoading(false);
                axios({
                    url: props.backEnd+'/account/mark',
                    method:'POST',
                    data: { accessToken: localStorage.getItem('accessToken'), mangaUrl: res?.data?.moreData?.formatedRoutes?.mangaSekaichapterArUrl, seen: true}
                }).then(res => {
                    setSeen(res.data.seen);
                }).catch(err => setError("a7a"))
            } else if (!res.data.chapter) {
                setMore(false)
            }
        }).catch(err => setError("a7a"));
    }
    console.log(data?.images?.length)

    useEffect(() => {
        setLoading(true);
        axios({
            url: props.backEnd+'/manga/chapter',
            method:'POST',
            data: {url : `${mangaid}/${chapterid}`}
        }).then(res => {
            let bruh = [...res.data.images, `end=${res.data.chapterName},${res.data.next.arChapter}`]
            setData({...res.data, images:bruh });
            setLoading(false);
        }).catch(err => setError("a7a"))
        axios({
            url: props.backEnd+'/account/mark',
            method:'POST',
            data: { accessToken: localStorage.getItem('accessToken'), mangaUrl: `${mangaid}/${chapterid}`, seen: true}
        }).then(res => {
            setSeen(res.data.seen);
        }).catch(err => setError("a7a"))
    }, [error, mangaid, chapterid, props.backEnd])




    return (
        <div className =" relative">
            {<div className ={`${isOpen ? "" : "opacity-0 pointer-events-none"}`} >
                <Navbar props = {{title: data.chapterHeading ? data.mangaName : 'Loading...', titleHref: data?.next?.formatedRoutes?.mangaSekaiUrl, secTitle: data.chapterHeading ? data.chapterName : 'Loading...',  backEnd: props.backEnd}}></Navbar>
                <MangaReaderFooter props = {{next: data?.next, prev: data?.prev}}></MangaReaderFooter>
            </div>
            }
            <button className = {`fixed mx-2 mt-2 ${isOpen ? "top-[4.5rem] " : "top-0 left-0"} z-20`} onClick ={() => setOpen(!isOpen)}>
                {isOpen ?
                    <svg xmlns="http://www.w3.org/2000/svg" class="h-9 w-9 text-sec" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M6 18L18 6M6 6l12 12" />
                    </svg> 
                : <svg xmlns="http://www.w3.org/2000/svg" class="h-9 w-9 text-sec " fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M4 6h16M4 12h16M4 18h16" />
              </svg>}
            </button>
            {loading ? <Loader
                        as='div'
                        className= "flex flex-col items-center mt-[30vh]"
                        scrollThreshold= {0.95}
                        type="TailSpin"
                        color="#DCDCDC"
                        height={60}
                        width={60}
                    /> : <div className ={`${isOpen ? "my-14" : "my-0.5"}`}>
                       
                            {data?.images?.map?.((img, index) => {
                                if (data.images.length-1 == index) return (<>
                                    <ImageLoader  props = {{backEnd: props.backEnd, img: img}}></ImageLoader>
                                    <h1 ref={lastElementRef} ></h1>
                                </>)
                                else return (<ImageLoader key={index} props = {{backEnd: props.backEnd, img: img}}></ImageLoader>)
                            })}
                            {!hasMore && <div className = "flex flex-col items-center mt-4 py-4 bg-thr w-full">
                                    <h1 className ="text-white text-xl">No More Chapters</h1>
                                </div>}
                            {cloading && <Loader
                                as='div'
                                className= "flex flex-col items-center mt-4"
                                type="TailSpin"
                                color="DCDCDC"
                                height={60}
                                width={60}
                            />}                 
                    </div>}
            

        </div>
    )
}

export default MangaReader
