import React, { useState } from "react";
import { useEffect } from "react";
import Loader from "react-loader-spinner";
import lazyImg from "./lazyImg";
import { LazyLoadImage } from "react-lazy-load-image-component";

function ImageLod(props) {
  var { src, lclass, height, width } = props.props || {};
  if (!src) src = props.src;
  var [loading, setLoading] = useState(true);
  var [img, setImg] = useState();
  var [err, setErr] = useState(false);
  let mainServer = src?.includes("heroku");
  useEffect(() => {
    let cors = ["anilist", "mangastarz", "discord", "heroku"];
    const checker = (value) => cors.some((element) => value?.includes(element));

    if (checker(src)) {
      setImg(src);
      setTimeout(() => {
        setLoading(false);
      }, 1000);
      return;
    }
    fetch(src, {})
      .then((response) => response?.blob())
      .then((imageBlob) => {
        const imageObjectURL = URL.createObjectURL(imageBlob);
        setImg(imageObjectURL);
        setLoading(false);
      })
      .catch((err) => {
        setImg(src);
        setLoading(false);
      });
  }, [src, err]);

  function Loaded() {
    setTimeout(() => {
      setLoading(false);
    }, 350);
  }

  function ErrorF() {
    setErr(true);
  }
  return (
    <>
      <div className={`${err ? "" : "hidden"}`}>
        <img
          className={`${
            props.className
          } transition duration-500 ${"opacity-100"} `}
          key={src}
          src={
            "https://media.discordapp.net/attachments/855961645238714438/921087060889174096/No-Image-Placeholder.svg.png"
          }
        />
      </div>
      <div className={`${err ? "hidden" : ""}`}>
        {loading ? (
          <div
            className={`${
              lclass?.includes("relative")
                ? ""
                : "absolute top-0 bottom-0 left-[25%] right-[25%]"
            } flex flex-col items-center justify-center ${lclass} min-h-[2rem]`}
          >
            {" "}
            <Loader
              as="div"
              type="TailSpin"
              color="#DCDCDC"
              height={height || 50}
              width={width || 50}
            />{" "}
          </div>
        ) : null}

        <img
          key={src}
          className={`${props.className} transition-all duration-500 ${
            loading ? "opacity-50" : "opacity-100"
          }`}
          src={img}
          onLoad={Loaded}
          onError={ErrorF}
        />
      </div>
    </>
  );
}

export default ImageLod;
