import React, { useEffect, useState } from 'react'
import Navbar from '../components/Navbar'
import axios from 'axios';
import InfiniteScroll from "react-infinite-scroll-component";
import Loader from "react-loader-spinner";
import MangaList from "../components/MangaList"
function Latest({ props }) {
    const [error, setError] = useState([]);
    const [refresh, setRefresh] = useState([]);

    const [loading, setLoading] = useState(true);
    const [data, setData] = useState([]);
    useEffect(() => {
        setLoading(true);
        axios({
            url: props.backEnd+'/manga/latest',
            method:'POST',
            data: {page : 0}
        }).then(res => {
            setData(res.data);
            setLoading(false);
        }).catch(err => setError("a7a"))
    }, [error, refresh, props.backEnd])

    function handelSearchValue(ref) {
        setLoading(true);
        axios({
            url: props.backEnd+'/manga/search',
            method:'POST',
            data: {title : ref.current.value}
        }).then(res => {
            setData(res.data);
            setLoading(false);
        }).catch(err => setError("a7a"))
    }

    return (
        <div className ="animate-fadein">
            <Navbar props={ {title: 'Search', search: true, handelSearchValue: handelSearchValue, backEnd: props.backEnd} } />
            <div className = "mt-14">
                {loading ? <Loader
                        as='div'
                        className= "flex flex-col items-center mt-[35vh]"
                        type="TailSpin"
                        color="#DCDCDC"
                        height={60}
                        width={60}
                        timeout={18 * 1000} //3 secs
                    />  :

                <InfiniteScroll
                    dataLength={data.length} //This is important field to render the next data
                    loader={<Loader
                        as='div'
                        className= "flex flex-col items-center"
                        type="TailSpin"
                        color="#DCDCDC"
                        height={50}
                        width={50}
                    />}

                    // below props only if you need pull down functionality
                    refreshFunction={() => {
                        setRefresh([]);
                    }}

                    pullDownToRefresh
                    pullDownToRefreshThreshold={50}
                    pullDownToRefreshContent={
                        <div className = "flex flex-col items-center -mt-4">
                            <svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-8 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                            </svg>
                        </div>
                    }
                    releaseToRefreshContent={
                        <div className = "flex flex-col items-center">
                            <svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-8 text-white animate-spin -mt-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                            </svg>
                        </div>    
                    }
                    >
                    <MangaList props = {{loading, data}} />

                </InfiniteScroll>}
            </div>

        </div>
    )
}

export default Latest
