import { useParams } from "react-router-dom";
import React, { useEffect, useState, useRef } from "react";
import Navbar from "../components/Navbar";
import axios from "axios";
import Loader from "react-loader-spinner";
import Chapter from "../components/Chapter";
import Popup from "../components/Popup";
import { Link } from "react-router-dom";
import ImageLod from "../components/ImageLod";
import MangaList from "../components/MangaList";
import Mangacard from "../components/Mangacard";

function useClickOutside(ref, callback) {
  useEffect(() => {
    function handleClickOutside(event) {
      if (ref.current && !ref.current.contains(event.target)) {
        callback();
      }
    }
    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [ref]);
}

function Manga({ props }) {
  let { mangaid } = useParams();
  mangaid = String(encodeURI(mangaid));

  const [data, setData] = useState({});

  const [rdata, setRData] = useState({});

  const [accountData, setAccountData] = useState();
  const [listData, setListData] = useState({});

  const [isOpen, setOpen] = useState(false);
  const [isCOpen, setCOpen] = useState(false);

  const [loading, setLoading] = useState(true);
  const [cLoading, setCLoading] = useState(true);
  const [rLoading, setRLoading] = useState(true);

  const [error, setError] = useState();
  const wrapperRef = useRef(null);
  useClickOutside(wrapperRef, () => setOpen(false));

  useEffect(() => {
    setLoading(true);
    setCLoading(true);
    setRLoading(true);

    axios({
      url: props.backEnd + "/manga/manga",
      method: "POST",
      data: { url: mangaid },
    })
      .then((res) => {
        setData(res.data);
        setLoading(false);
        var mangaData = res.data;

        axios({
          url: props.backEnd + "/manga/chapters",
          method: "POST",
          data: { mangaid: mangaData.madaraid },
        })
          .then((res) => {
            setData({ ...mangaData, ...res.data });
            setCLoading(false);
            axios({
              url: props.backEnd + "/manga/mangaRecommendations",
              method: "POST",
              data: { moreInfo: mangaData.moreInfo },
            })
              .then((res) => {
                setRData({ ...res.data });
                setRLoading(false);
              })
              .catch((err) => setError("a7a"));
          })
          .catch((err) => setError("a7a"));
      })
      .catch((err) => setError("a7a"));
    axios({
      url: props.backEnd + "/account/manga",
      method: "POST",
      data: {
        mangaUrl: mangaid,
        accessToken: localStorage.getItem("accessToken"),
      },
    })
      .then((res) => {
        setAccountData(res.data);
        setListData(res.data.mylists);
      })
      .catch((err) => setError("a7a"));
  }, [error, mangaid, props.backEnd]);

  let contChapter = accountData?.db?.chapters
    ?.filter?.((x) => x?.seen)
    ?.reverse()?.[0];
  let cChapter = data?.chapters?.find(
    (x) => x?.moreData?.db?.chapterNum === contChapter?.chapterNum
  );

  function handelMylistClick(list) {
    axios({
      url: props.backEnd + "/account/manageMylist",
      method: "POST",
      data: {
        accessToken: localStorage.getItem("accessToken"),
        list: list,
        mangaData: { ...data, chapters: [] },
        mangaUrl: data.moreData.formatedRoutes.mangaUrl,
      },
    }).then((res) => {
      if (res.data.success) {
        let listDataCopy = {};
        listData[list] = !res.data.removed;
        setListData({ ...listData, listDataCopy });
      }
    });
  }
  //;

  return (
    <>
      <Navbar props={{ title: "Manga", backEnd: props.backEnd }}></Navbar>
      {loading ? (
        <Loader
          as="div"
          className="flex flex-col items-center mt-[35vh]"
          type="TailSpin"
          color="#DCDCDC"
          height={60}
          width={60}
        />
      ) : (
        <div className = "h-full md:py-0">
          <div className=" animate-fadeinslow relative bg-thr mx-2 md:mx-12 lg:mx-24 xl:60 2xl:mx-80 rounded-xl mt-24 py-4 md:py-0">
            <div className="flex flex-col md:flex-row items-center justify-end h-full">
              <div className="relative h-full md:hidden">
                <ImageLod
                  className="object-fill rounded h-full max-h-[300px]"
                  src={data?.moreInfo?.coverImage?.large || data.image}
                />
                <ImageLod
                  className="absolute w-auto h-[25px] rounded left-1 top-2  shadow-2xl"
                  src={data?.moreInfo?.flag}
                  alt={data?.moreInfo?.countryOfOrigin}
                />
              </div>
              <div className="flex flex-col items-center md:items-end mx-4 space-y-3">
                <h1 className="text-2xl text-white w-full text-center md:text-right mt-1.5">
                  {data.name}
                </h1>
                <div className="flex flex-col items-center md:items-end space-y-0.5">
                  <div className="flex items-center">
                    <h1 className="text-md text-white w-full text-center md:text-right my-0 flex items-center mr-1">
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        className="h-6 w-6 mr-0.5 text-yellow-500"
                        fill="none"
                        viewBox="0 0 24 24"
                        stroke="currentColor"
                      >
                        <path
                          strokeLinecap="round"
                          strokeLinejoin="round"
                          strokeWidth="2"
                          d="M11.049 2.927c.3-.921 1.603-.921 1.902 0l1.519 4.674a1 1 0 00.95.69h4.915c.969 0 1.371 1.24.588 1.81l-3.976 2.888a1 1 0 00-.363 1.118l1.518 4.674c.3.922-.755 1.688-1.538 1.118l-3.976-2.888a1 1 0 00-1.176 0l-3.976 2.888c-.783.57-1.838-.197-1.538-1.118l1.518-4.674a1 1 0 00-.363-1.118l-3.976-2.888c-.784-.57-.38-1.81.588-1.81h4.914a1 1 0 00.951-.69l1.519-4.674z"
                        />
                      </svg>
                      {data?.moreInfo?.averageScore / 10 || data?.rating?.score}
                      <span className="text-[0.70rem] text-gray-100 ml-[1px]">
                        ({data?.moreInfo?.popularity || data?.rating?.ratedBy})
                      </span>
                    </h1>
                    <h1 className="text-md text-white w-full text-center md:text-right my-0 flex items-center mr-1">
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        className="h-6 w-6 mr-0.5"
                        fill="none"
                        viewBox="0 0 24 24"
                        stroke="currentColor"
                      >
                        <path
                          strokeLinecap="round"
                          strokeLinejoin="round"
                          strokeWidth="2"
                          d="M5.636 18.364a9 9 0 010-12.728m12.728 0a9 9 0 010 12.728m-9.9-2.829a5 5 0 010-7.07m7.072 0a5 5 0 010 7.07M13 12a1 1 0 11-2 0 1 1 0 012 0z"
                        />
                      </svg>
                      {data.status}
                    </h1>
                    <h1 className="text-md text-white w-full text-center md:text-right my-0 flex items-center mr-1">
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        className="h-6 w-6 mr-0.5"
                        fill="none"
                        viewBox="0 0 24 24"
                        stroke="currentColor"
                      >
                        <path
                          strokeLinecap="round"
                          strokeLinejoin="round"
                          strokeWidth="2"
                          d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z"
                        />
                      </svg>
                      {data?.moreInfo?.startDate?.year || data.year}
                    </h1>
                  </div>

                  {data?.moreInfo?.success ? (
                    <div className="flex items-center">
                      <h1 className="text-[0.65] text-white w-full text-center md:text-right my-0 flex items-center mr-1">
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          className="h-6 w-6 mr-0.5"
                          fill="none"
                          viewBox="0 0 24 24"
                          stroke="currentColor"
                        >
                          <path
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            strokeWidth="1.5"
                            d="M12 6.253v13m0-13C10.832 5.477 9.246 5 7.5 5S4.168 5.477 3 6.253v13C4.168 18.477 5.754 18 7.5 18s3.332.477 4.5 1.253m0-13C13.168 5.477 14.754 5 16.5 5c1.747 0 3.332.477 4.5 1.253v13C19.832 18.477 18.247 18 16.5 18c-1.746 0-3.332.477-4.5 1.253"
                          />
                        </svg>

                        <span className="whitespace-nowrap">
                          {data?.moreInfo?.source
                            ?.toLowerCase?.()
                            ?.replace?.(/[^aA-zZ]/g, "")
                            ?.replace?.(/_/g, " ")
                            ?.replace(/ ./g, (x) => x?.toUpperCase?.())
                            ?.replace(/./, (x) => x?.toUpperCase?.()) ||
                            " "}
                        </span>
                      </h1>

                      {data?.moreInfo?.staff?.edges
                        ?.slice(0, 2)
                        ?.map?.((staff, i) => {
                          return (
                            <h1
                              key={i}
                              className="text-[0.80rem] text-white w-full text-center md:text-right my-0 flex items-center mr-1"
                            >
                              <svg
                                xmlns="http://www.w3.org/2000/svg"
                                className="h-6 w-5 mr-0.5"
                                fill="none"
                                viewBox="0 0 24 24"
                                stroke="currentColor"
                              >
                                <path
                                  strokeLinecap="round"
                                  strokeLinejoin="round"
                                  strokeWidth="1.5"
                                  d="M15.232 5.232l3.536 3.536m-2.036-5.036a2.5 2.5 0 113.536 3.536L6.5 21.036H3v-3.572L16.732 3.732z"
                                />
                              </svg>
                              <span className="whitespace-nowrap">
                                {staff?.node?.name?.full}
                              </span>
                            </h1>
                          );
                        })}
                    </div>
                  ) : (
                    ""
                  )}
                </div>

                <div className="flex items-right justify-end space-x-1 ">
                  {data.genres?.map((g, i) => {
                    return (
                      <div key={i} className="flex items-center">
                        <h1 className="text-xs text-white inline-block px-[0.150rem] py-[0.150rem] md:px-1.5 border-2 border-sec rounded-lg max-w-[85px] text-center ">
                          {g}
                        </h1>
                      </div>
                    );
                  })}
                </div>
                <div className="flex items-center space-x-3 mt-2">
                  <button
                    onClick={() => setCOpen(!isCOpen)}
                    className={`${
                      data?.moreInfo?.characters?.edges?.length > 0
                        ? ""
                        : "hidden"
                    } transition duration-500 hover:bg-sec flex items-center text-center border-2 border-sec rounded pl-1 pr-2 py-1.5`}
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      className="h-5 w-6 text-gray-100 mr-1"
                      width="404.118"
                      height="436"
                      viewBox="0 0 404.118 436"
                    >
                      <g id="person-outline" transform="translate(-47.937 -32)">
                        <path
                          id="Path_16"
                          data-name="Path 16"
                          d="M344,144c-3.92,52.87-44,96-88,96s-84.15-43.12-88-96c-4-55,35-96,88-96S348,90,344,144Z"
                          fill="none"
                          stroke="#fff"
                          strokeLinecap="round"
                          strokeLinejoin="round"
                          strokeWidth="32"
                        />
                        <path
                          id="Path_17"
                          data-name="Path 17"
                          d="M256,304c-87,0-175.3,48-191.64,138.6C62.39,453.52,68.57,464,80,464H432c11.44,0,17.62-10.48,15.65-21.4C431.3,352,343,304,256,304Z"
                          fill="none"
                          stroke="#fff"
                          strokeMiterlimit="10"
                          strokeWidth="32"
                        />
                      </g>
                    </svg>
                    <span className="text-gray-200 text-center">
                      Characters
                    </span>
                  </button>
                  <button
                    onClick={() => setOpen(!isOpen)}
                    className={`${
                      accountData ? "" : "hidden"
                    } transition duration-500 hover:bg-sec flex items-center text-center border-2 border-sec rounded pl-1 pr-2 py-1`}
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      className="h-7 w-6 text-gray-100 mr-1"
                      fill="none"
                      viewBox="0 0 24 24"
                      stroke="currentColor"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth="2"
                        d="M12 4v16m8-8H4"
                      />
                    </svg>
                    <span className="text-gray-200 text-center">
                      Manage My List
                    </span>
                  </button>
                </div>

                <div>
                  <h1 className="text-gray-200 text-center"></h1>
                  <div className="border-t-2 border-gray-600 pt-2 text-right mx-0">
                    <h1
                      className="text-gray-200 max-w-[500px] max-h-[100px] overflow-y-scroll"
                      dir="rtl"
                    >
                      {data.story || (
                        <span>
                                <br />
                          {data?.moreInfo?.description
                            ?.replace?.(/<br>/g, "\n")
                            ?.replace(/"/g, "") || ""}
                        </span>
                      )}{" "}
                    </h1>
                  </div>
                </div>
                <div></div>
              </div>
              <div className="relative h-full hidden md:block">
                <ImageLod
                  className="object-fill rounded h-full"
                  src={data?.moreInfo?.coverImage?.large || data.image}
                  alt=".."
                />
                <ImageLod
                  className="absolute w-auto h-[35px] rounded left-1 top-2"
                  src={data?.moreInfo?.flag}
                  alt={data?.moreInfo?.countryOfOrigin}
                />
              </div>
            </div>
          </div>
          <Popup
            props={{
              title: `Characters`,
              children: (
                <div className="grid grid-flow-row grid-cols-3 md:grid-cols-4 lg:grid-cols-5 xl:grid-cols-6 my-2 gap-x-2.5 mx-2.5 gap-y-2.5">
                  {data?.moreInfo?.characters?.edges?.map((chr, i) => {
                    return (
                     <Mangacard props={{manga: {image: chr?.node?.image?.large, name: chr?.node?.name?.full, year: chr?.role}}}></Mangacard>
                    );
                  })}
                </div>
              ),
              isOpen: isCOpen,
              handelClose: () => setCOpen(false),
            }}
          ></Popup>
          <div
            className={`transition-all duration-500 z-30 fixed top-0 bottom-0 left-0 right-0 bg-black/60 ${
              isOpen ? "opacity-100" : "opacity-0 pointer-events-none"
            }`}
          >
            <div
              ref={wrapperRef}
              className={`transition-all duration-[400ms] ${
                isOpen ? "scale-100 top-[48%]" : " top-[90%] scale-0"
              } fixed mx-2 lg:mx-[25%] bottom-0 left-0 right-0  bg-main rounded-md flex flex-col`}
            >
              <h1 className="text-gray-100 text-3xl text-center mt-4">
                Manage My List
              </h1>
              <div className="flex flex-col mt-4 mx-4 space-y-4">
                <button
                  onClick={() => handelMylistClick("favorite")}
                  className={`transition duration-200 text-gray-100 text-[25px] flex items-center ${
                    listData?.favorite ? "bg-sec" : "bg-thr"
                  } rounded-md`}
                >
                  <div className="flex items-center mx-2 my-2">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      className="h-8 w-8 text-gray-100 mr-2"
                      fill="none"
                      viewBox="0 0 24 24"
                      stroke="currentColor"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth="2"
                        d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z"
                      />
                    </svg>
                    <h1>Favorite</h1>
                  </div>
                </button>
                <button
                  onClick={() => handelMylistClick("watching")}
                  className={`transition duration-200 text-gray-100 text-[25px] flex items-center ${
                    listData?.watching ? "bg-sec" : "bg-thr"
                  } rounded-md`}
                >
                  <div className="flex items-center mx-2 my-2">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      className="h-8 w-8 text-gray-100 mr-2"
                      fill="none"
                      viewBox="0 0 24 24"
                      stroke="currentColor"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth="2"
                        d="M14.752 11.168l-3.197-2.132A1 1 0 0010 9.87v4.263a1 1 0 001.555.832l3.197-2.132a1 1 0 000-1.664z"
                      />
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth="2"
                        d="M21 12a9 9 0 11-18 0 9 9 0 0118 0z"
                      />
                    </svg>
                    <h1>Currently Watching</h1>
                  </div>
                </button>
                <button
                  onClick={() => handelMylistClick("planToWatch")}
                  className={`transition duration-200 text-gray-100 text-[25px] flex items-center ${
                    listData?.planToWatch ? "bg-sec" : "bg-thr"
                  }  rounded-md`}
                >
                  <div className="flex items-center mx-2 my-2">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      className="h-8 w-8 text-gray-100 mr-2"
                      fill="none"
                      viewBox="0 0 24 24"
                      stroke="currentColor"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth="2"
                        d="M9 5H7a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2m-6 9l2 2 4-4"
                      />
                    </svg>
                    <h1>Plan To Watch</h1>
                  </div>
                </button>
                <button
                  onClick={() => handelMylistClick("completed")}
                  className={`transition duration-200 text-gray-100 text-[25px] flex items-center ${
                    listData?.completed ? "bg-sec" : "bg-thr"
                  }  rounded-md`}
                >
                  <div className="flex items-center mx-2 my-2">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      className="h-8 w-8 text-gray-100 mr-2"
                      fill="none"
                      viewBox="0 0 24 24"
                      stroke="currentColor"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth="2"
                        d="M5 13l4 4L19 7"
                      />
                    </svg>
                    <h1>Completed</h1>
                  </div>
                </button>
                <button
                  onClick={() => handelMylistClick("dropped")}
                  className={`transition duration-200 text-gray-100 text-[25px] flex items-center ${
                    listData?.dropped ? "bg-sec" : "bg-thr"
                  }  rounded-md`}
                >
                  <div className="flex items-center mx-2 my-2">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      className="h-8 w-8 text-gray-100 mr-2"
                      fill="none"
                      viewBox="0 0 24 24"
                      stroke="currentColor"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth="2"
                        d="M6 18L18 6M6 6l12 12"
                      />
                    </svg>
                    <h1>Dropped</h1>
                  </div>
                </button>
              </div>
            </div>
          </div>

          <div
            className={`bg-thr py-2 mt-6 mx-2 md:mx-12 lg:mx-24 xl:60 2xl:mx-80 rounded-xl mb-4 ${
              rLoading == false && !(rdata?.recommendations?.length > 0)
                ? "hidden"
                : ""
            }`}
          >
            {rLoading ? (
              <h1 className="text-2xl text-white w-full text-center mb-4">
                Recommendations
              </h1>
            ) : rdata?.recommendations?.length > 0 ? (
              <h1 className="text-2xl text-white w-full text-center mb-4">
                Recommendations
              </h1>
            ) : (
              ""
            )}

            {rLoading ? (
              <Loader
                as="div"
                className="flex flex-col items-center my-[10vh]"
                type="TailSpin"
                color="#DCDCDC"
                height={100}
                width={100}
              />
            ) : (
              <>
                <div>
                  {rdata?.recommendations?.length > 0 ? (
                    <MangaList
                      props={{
                        loading: rLoading,
                        data: rdata?.recommendations,
                        x: true,
                      }}
                    />
                  ) : (
                    ""
                  )}
                </div>
              </>
            )}
          </div>
          <div className="bg-thr py-2 mt-6 mx-2 md:mx-12 lg:mx-24 xl:60 2xl:mx-80 rounded-xl mb-20 ">
            {cLoading ? (
              <Loader
                as="div"
                className="flex flex-col items-center my-[10vh]"
                type="TailSpin"
                color="#DCDCDC"
                height={100}
                width={100}
              />
            ) : (
              <>
                <div className="grid grid-cols-2 border-b-2 border-gray-600 py-6">
                  <div className="transition duration-500 border-sec hover:bg-black/60 border-2 mx-4 flex flex-col px-2 py-1 rounded">
                    <Link
                      to={
                        data?.latestCh?.moreData.formatedRoutes
                          .mangaSekaichapterArUrl
                      }
                    >
                      <h1 className="text-lg text-white text-right">
                         
                      </h1>
                      <h1 className="text-lg text-white text-right">
                        {data?.latestCh?.num}
                      </h1>
                      <h1 className=" text-gray-300 text-md text-right -mt-1.5">
                        {data?.latestCh?.date}
                      </h1>
                    </Link>
                  </div>
                  <div className="transition duration-500 border-sec hover:bg-black/60 border-2 mx-4 flex flex-col px-2 py-1 rounded">
                    {cChapter ? (
                      <Link
                        to={
                          cChapter.moreData.formatedRoutes
                            .mangaSekaichapterArUrl
                        }
                      >
                        <h1 className="text-lg text-white text-right">
                           {" "}
                        </h1>
                        <h1 className="text-lg text-white text-right">
                          {cChapter?.num}
                        </h1>
                        <h1 className="text-md text-gray-300 text-right -mt-1">
                          {cChapter?.date}
                        </h1>
                      </Link>
                    ) : (
                      <Link
                        to={
                          data?.firstCh?.moreData.formatedRoutes
                            .mangaSekaichapterArUrl
                        }
                      >
                        <h1 className="text-lg text-white text-right">
                           {" "}
                        </h1>
                        <h1 className="text-lg text-white text-right">
                          {data?.firstCh?.num}
                        </h1>
                        <h1 className="text-md text-gray-300 text-right -mt-1">
                          {data?.firstCh?.date}
                        </h1>
                      </Link>
                    )}
                  </div>
                </div>
                <div
                  className="grid grid-cols-2 md:grid-cols-4 gap-y-2 py-6 items-center md:max-h-[24rem] overflow-y-scroll"
                  dir="ltr"
                >
                  {data?.chapters?.map?.((ch, i) => {
                    return (
                      <Chapter
                        key={i}
                        props={{
                          ch,
                          accountData,
                          backEnd: props.backEnd,
                          mangaData: data,
                        }}
                      ></Chapter>
                    );
                  })}
                </div>
              </>
            )}
          </div>
        </div>
      )}
    </>
  );
}

export default Manga;
