import React, { useEffect, useState } from 'react'
import Navbar from '../components/Navbar'
import axios from 'axios';
import InfiniteScroll from "react-infinite-scroll-component";
import Loader from "react-loader-spinner";
import MangaList from "../components/MangaList"

function Mylist({ props }) {
    const [error, setError] = useState([]);
    const [list, setList] = useState('favorite');
    
    const [loading, setLoading] = useState(true);
    const [data, setData] = useState([]);
    
    useEffect(() => {
        setLoading(true);
        axios({
            url: props.backEnd+'/account/myList',
            method:'POST',
            data: { accessToken: localStorage.getItem('accessToken'), list: list }
        }).then(res => {
            setData(res.data.db);
            setLoading(false);
        }).catch(err => setError("a7a"))
    }, [error, list, props.backEnd])



    return (
        <div className ="animate-fadein">
            <Navbar props={ {title: 'My List', backEnd: props.backEnd} } />
            <div className = "flex flex-row items-center justify-center bg-thr mt-12">
                <div className = "mx-3 flex items-center space-x-3 overflow-x-scroll pb-2">
                    <button onClick = {() => setList('favorite')} className = {`ml-2 my-1 transition duration-200 py-1 px-2 active:scale-110 rounded border border-sec ${ list === 'favorite' ? 'bg-sec scale-110': 'bg-thr'} whitespace-nowrap`}>
                        <h1  className = "text-white ">Favorite</h1>
                    </button>
                    <button onClick = {() => setList('watching')} className = {`transition duration-200 active:scale-110 py-1 px-2 rounded border border-sec ${ list === 'watching' ? 'bg-sec scale-110': 'bg-thr'} whitespace-nowrap`}>
                        <h1  className = "text-white ">Watching</h1>
                    </button>
                    <button onClick = {() => setList('planToWatch')} className = {`transition duration-200 active:scale-110 py-1 px-2 rounded border border-sec ${ list === 'planToWatch' ? 'bg-sec scale-110': 'bg-thr'} whitespace-nowrap`}>
                        <h1  className = "text-white ">Plan To Watch</h1>
                    </button>
                    <button onClick = {() => setList('completed')} className = {`transition duration-200 active:scale-110 py-1 px-2 rounded border border-sec ${ list === 'completed' ? 'bg-sec scale-110': 'bg-thr'} whitespace-nowrap`}>
                        <h1  className = "text-white ">Completed</h1>
                    </button>
                    <button onClick = {() => setList('dropped')} className = {`mr-1 transition duration-200 active:scale-110 py-1 px-2 rounded border border-sec ${ list === 'dropped' ? 'bg-sec scale-110': 'bg-thr'} whitespace-nowrap`}>
                        <h1  className = "text-white ">Dropped</h1>
                    </button>
                </div>
            </div>
            <div className = "">
                {loading ? <Loader
                        as='div'
                        className= "flex flex-col items-center mt-[35vh]"
                        type="TailSpin"
                        color="#DCDCDC"
                        height={60}
                        width={60}
                        timeout={60 * 1000} //3 secs
                    />  :

                <InfiniteScroll
                    dataLength={data.length} //This is important field to render the next data
                    loader={<Loader
                        as='div'
                        className= "flex flex-col items-center"
                        type="TailSpin"
                        color="#DCDCDC"
                        height={50}
                        width={50}
                        timeout={6 * 1000} //3 secs
                    />}

             
                    >
                    <MangaList props = {{loading, data}} />

                </InfiniteScroll>}
            </div>

        </div>
    )
}

export default Mylist
