import React, {useRef, useEffect } from "react";
import {
  useLocation,
  Link
} from "react-router-dom";
function useClickOutside(ref, callback) {
    useEffect(() => {
        function handleClickOutside(event) {
            if (ref.current && !ref.current.contains(event.target)) {
              callback()
            }
        }
        document.addEventListener("mousedown", handleClickOutside);
        return () => {
            document.removeEventListener("mousedown", handleClickOutside);
        };
    }, [ref]);
}

function Sidebar({props}) {
  var location = useLocation();
  var routes = [
    {
      path: '/',
      title: 'Latest',
      icon: <svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-8 mr-2 text-gray-100" fill="none" viewBox="0 0 24 24" stroke="currentColor">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M17.657 18.657A8 8 0 016.343 7.343S7 9 9 10c0-2 .5-5 2.986-7C14 5 16.09 5.777 17.656 7.343A7.975 7.975 0 0120 13a7.975 7.975 0 01-2.343 5.657z" />
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9.879 16.121A3 3 0 1012.015 11L11 14H9c0 .768.293 1.536.879 2.121z" />
      </svg>,
    },
    {
      path: '/mangalist',
      title: 'Manga List',
      icon: <svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-8 mr-2 text-gray-100" fill="none" viewBox="0 0 24 24" stroke="currentColor">
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9 5H7a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2m-3 7h3m-3 4h3m-6-4h.01M9 16h.01" />
      </svg>,
    },
    {
      path: '/mylist',
      title: 'My List',
      icon: <svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-8 mr-2 text-gray-100" fill="none" viewBox="0 0 24 24" stroke="currentColor">
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9 5H7a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2m-3 7h3m-3 4h3m-6-4h.01M9 16h.01" />
      </svg>,
    },
    {
      path: '/lastwatched',
      title: 'Last Watched',
      icon: <svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-8 mr-2 text-gray-100" fill="none" viewBox="0 0 24 24" stroke="currentColor">
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
      </svg>,
    },
    {
      path: '/downloads',
      title: 'Downloads',
      icon: <svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-8 mr-2 text-gray-100" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M4 16v1a3 3 0 003 3h10a3 3 0 003-3v-1m-4-4l-4 4m0 0l-4-4m4 4V4" />
            </svg>,
    },
    
]

  const wrapperRef = useRef(null);
  useClickOutside(wrapperRef, props.handelClose);
  

  return (

    <div className = {`transition-all duration-500 fixed top-0 bottom-0 left-0 right-0 ${props.isOpen ? "opacity-100": "opacity-0 pointer-events-none"} bg-black/60 z-50`}>
        <div className = {`z-50 overflow-y-auto transition-all transform-gpu duration-[500ms] ease-in-out fixed top-0 bottom-0 left-0 bg-main flex flex-col w-[60%] max-w-[360px] ${props.isOpen ? "": "left-[-900px]"}  `} ref = {wrapperRef}>
          <button onClick={props.handelClose} className = "mx-2 py-1">
            <svg xmlns="http://www.w3.org/2000/svg" className="h-10 w-10 text-gray-100" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>

            <div className="flex flex-col justify-between h-full">
              <div>
                <Link to ={ props.user.username ? "/profile/"+props.user.id : `/login`}>
                  <div className = " relative border-b-2 border-gray-800">
                    <h1 className = "absolute bottom-1 left-1 bg-thr text-white px-2 py-1 rounded-md">
                      <span className ="text-sec mr-2">{props.user.role || "Guest"} |</span>
                      {props.user.username || "Login"}
                    </h1>
                    <img className = "w-full h-[200px] object-center object-cover" src={props.user.image || "https://media.discordapp.net/attachments/855961645238714438/912739170332606494/person-outline-free-vector-icon-800x566.png"} alt="" />
                  </div>
                </Link>
                <div className = 'mt-4 mx-2 flex-col  space-y-3.5 '>
                  {routes.map(route => {
                    return (<Link key={route.path} to={route.path} className={`transition duration-200 text-gray-100 text-[25px] flex items-center ${location.pathname === route.path ? `bg-sec` : "bg-thr "} hover:bg-sec rounded`}>
                    <div className = "flex items-center mx-2 my-1">
                      {route.icon}
                      <h1>{route.title}</h1>
                    </div>
                  </Link>)
                  })}
                </div>
              </div>
              <div className = "mx-2 mb-4 mt-2">
                <Link to="/settings" className={`transition duration-200 text-gray-100 text-[25px] flex items-center ${location.pathname === '/settings' ? `bg-sec` : "bg-thr "} hover:bg-sec rounded-md`}>
                    <div className = "flex items-center mx-2 my-1">
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-8 mr-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M10.325 4.317c.426-1.756 2.924-1.756 3.35 0a1.724 1.724 0 002.573 1.066c1.543-.94 3.31.826 2.37 2.37a1.724 1.724 0 001.065 2.572c1.756.426 1.756 2.924 0 3.35a1.724 1.724 0 00-1.066 2.573c.94 1.543-.826 3.31-2.37 2.37a1.724 1.724 0 00-2.572 1.065c-.426 1.756-2.924 1.756-3.35 0a1.724 1.724 0 00-2.573-1.066c-1.543.94-3.31-.826-2.37-2.37a1.724 1.724 0 00-1.065-2.572c-1.756-.426-1.756-2.924 0-3.35a1.724 1.724 0 001.066-2.573c-.94-1.543.826-3.31 2.37-2.37.996.608 2.296.07 2.572-1.065z" />
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                    </svg>
                      <h1>Settings</h1>
                    </div>
                </Link>
              </div>
            </div>
        </div>
    </div>
  );
}



export default Sidebar
