import React from 'react'
import { Link } from 'react-router-dom'
import Mangacard from "./Mangacard"

function MangaList({ props }) {
    let { data, loading, x } = props
    return (
            <div className = {x ? "overflow-x-scroll flex items-center my-2 mx-2.5 gap-x-2.5 gap-y-2.5" : "grid grid-flow-row grid-cols-3 md:grid-cols-4 lg:grid-cols-5 xl:grid-cols-9 my-2 gap-x-2.5 mx-2.5 gap-y-2.5"}>
                {loading ? <div></div> : data?.map((manga, i) => {
                    return (
                        <Link key = {i} className = {`animate-fadeinslows ${x ? 'w-[150px] md:w-[190px] lg:w-[220px] xl:[250px] flex-shrink-0' :''}`} to={manga?.moreData?.formatedRoutes?.mangaSekaiUrl}>
                            <Mangacard key={manga?.moreData?.formatedRoutes?.mangaSekaiUrl} props={{manga}}></Mangacard>
                        </Link>
                    )
                })}
            </div>
    )
}

export default MangaList
