import React, { useState, useEffect } from "react";
import ImageLod from "./ImageLod";
import Loader from "react-loader-spinner";
import axios from "axios";

function ImageLoader({ props }) {
  var [data, setData] = useState();
  var [error, setError] = useState();


  return (
    <div className="relative flex flex-col items-center">
      {props.img.includes("end=") ? (
        <div className="bg-thr flex flex-col items-center py-6 space-y-2 w-full">
          <h1 className="text-white text-xl">
            End of chapter{" "}
            {props?.img?.split?.("=")?.[1]?.split?.(",")?.[0] || "Not Found"}
          </h1>
          <h1 className="text-white text-xl">
            Start of chapter{" "}
            {props?.img?.split?.("=")?.[1]?.split?.(",")?.[1] || "Not Found"}
          </h1>
        </div>
      ) : null}

      <ImageLod
        props={{
          lclass: "relative my-[5vh] mx-[5vw]",
          width: 100,
          height: 100,
        }}
        className="relative max-w-[98vw] min-w-full w-full h-auto animate-fadein lg:w-[60vw] min-h-[26rem]"
        src={
            props.img ? `${props.backEnd}/manga/getimg?url=${encodeURI(props.img)}` : ""
        }
        alt=""
      />
    </div>
  );

  /*

    return (
        <div className = 'relative flex flex-col items-center'>
            {props.img.includes('end=') ? <div className = "bg-thr flex flex-col items-center py-6 space-y-2 w-[100%] overflow-hidden">
                <h1 className ="text-white text-lg">End of chapter {props?.img?.split?.('=')?.[1]?.split?.(',')?.[0] || 'Not Found'}</h1> 
                <h1 className ="text-white text-lg">Start of chapter {decodeURI(props?.img?.split?.('=')?.[1]?.split?.(',')?.[1] || 'Not Found')?.replace?.(/-/g, " ")}</h1>
            </div> : 
            <ImageLod props ={{lclass: "relative my-[5vh] mx-[5vw]", width: 100, height:100}} className = "relative max-w-[98vw] w-full h-auto animate-fadein lg:max-w-[60vw]" src={props?.img?.includes('mangastarz') ? props?.img : `${props.backEnd}/manga/getimg?url=${encodeURI(props.img)}`}  alt="" />
        }
        </div>
    )*/
}

export default ImageLoader;
