import React, {useRef, useEffect } from "react";

function useClickOutside(ref, callback) {
    useEffect(() => {
        function handleClickOutside(event) {

            if (ref.current && !ref.current.contains(event.target)) {
              callback()
            }
        }
        document.addEventListener("mousedown", handleClickOutside);
        return () => {
            document.removeEventListener("mousedown", handleClickOutside);
        };
    }, [ref]);
}

function Popup({props}) {
  const wrapperReff = useRef(null);
  useClickOutside(wrapperReff, props.handelClose);
  

  return (

        <div className ={`transition-all duration-500 fixed z-30 top-0 h-full w-full bottom-0 left-0 right-0 bg-black/60 ${props.isOpen ? "opacity-100": "opacity-0 pointer-events-none"}`}>
            <div ref = {wrapperReff} className ={`transition-all duration-[400ms] ${props.isOpen ? "scale-100 top-[30%] lg:top-[40%]": " top-[90%] scale-0"} fixed mx-2 lg:mx-[20%] bottom-0 left-0 right-0  bg-main rounded-md flex flex-col`}>
                <h1  className = "text-gray-100 text-3xl text-center mt-4">{props.title}</h1>
                <div className = 'mt-4  overflow-scroll'>
                    {props.children}
                </div>
            </div>
        </div>
    
  );
}



export default Popup
