import React from 'react'



function Navbar({props}) {



    return (
        <div className ="fixed z-20 bottom-0 w-full bg-thr">
            <div className = "mx-2.5 py-2 flex justify-between items-center">
                <a href={props?.prev?.formatedRoutes?.mangaSekaichapterArUrl}>
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-7 w-8 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M10 19l-7-7m0 0l7-7m-7 7h18" />
                    </svg>
                </a>
                <a href={props?.next?.formatedRoutes?.mangaSekaichapterArUrl}>
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-7 w-8 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M14 5l7 7m0 0l-7 7m7-7H3" />
                    </svg>
                </a>

            </div>
        </div>
    )
}

export default Navbar
