import { useParams } from 'react-router-dom';
import React, { useEffect, useState } from 'react'
import Navbar from '../components/Navbar'
import axios from 'axios';
import Loader from "react-loader-spinner";
import ImageLoader from '../components/ImageLoader'
import { Link } from 'react-router-dom';


function MangaReader({props}) {
    let { mangaid, chapterid } = useParams()
    const [loading, setLoading] = useState(true);

    const [error, setError] = useState();
    const [data, setData] = useState({});
    const [isSeen, setSeen] = useState(false);
    const [isOpen, setOpen] = useState(true);

    
    useEffect(() => {
        setLoading(true);
        axios({
            url: props.backEnd+'/manga/chapter',
            method:'POST',
            data: {url : `${mangaid}/${chapterid}`}
        }).then(res => {
            setData({...res.data });
            setLoading(false);
        }).catch(err => setError("a7a"))
        axios({
            url: props.backEnd+'/account/mark',
            method:'POST',
            data: { accessToken: localStorage.getItem('accessToken'), mangaUrl: `${mangaid}/${chapterid}`, seen: true}
        }).then(res => {
            setSeen(res.data.seen);
        }).catch(err => setError("a7a"));
    }, [error, chapterid, mangaid, props.backEnd]);

    function changeSeen() {
        axios({
            url: props.backEnd+'/account/mark',
            method:'POST',
            data: { accessToken: localStorage.getItem('accessToken'), mangaUrl: `${mangaid}/${chapterid}`, seen: !isSeen}
        }).then(res => {
            setSeen(res.data.seen);
        }).catch(err => setError("a7a"))
    }

    return (
        <div className ="animate-fadein ">
            <div className ={`fixed overflow-hidden w-full transition duration-300 z-50 ${isOpen ? "" : "pointer-events-none opacity-0 "}`} > 
                <Navbar props = {{title: 'Chapter', backEnd: props.backEnd}}></Navbar>
            </div>
            {loading ? <Loader
                        as='div'
                        className= "flex flex-col items-center mt-[35vh]"
                        type="TailSpin"
                        color="#DCDCDC"
                        height={60}
                        width={60}
                        timeout={60 * 1000} //3 secs
                    /> : <div className = 'flex flex-col items-center mt-12 z-20' onClick={() => setOpen(!isOpen)}>
                        <div className = 'flex flex-col items-center my-2'>
                        <button onClick = {() => setOpen(!isOpen)}>IDK</button>

                            <Link to={data.moreData.formatedRoutes.mangaSekaiUrl} className = 'text-white text-2xl border-b-2 border-gray-600 pb-2' >{data.chapterHeading}</Link>
                            <div className = "flex flex-col items-center space-x-3 my-2 ">
                                <div className = "flex flex-row items-center space-x-3">
                                    <Link className = {`${data?.prev ? 'flex' : "hidden"} items-center py-1 px-3 bg-sec rounded`} to={data?.prev?.formatedRoutes?.mangaSekaichapterArUrl}>
                                        <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 mx-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M10 19l-7-7m0 0l7-7m-7 7h18" />
                                        </svg>
                                        Back
                                    </Link>
                                    <Link className = {`${data?.next ? 'flex' : "hidden"} items-center py-1 px-3 bg-sec rounded`} to={data?.next?.formatedRoutes?.mangaSekaichapterArUrl}>
                                        Next
                                        <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 mx-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M14 5l7 7m0 0l-7 7m7-7H3" />
                                        </svg>
                                    </Link>
                                </div>
                                
                                <button onClick= {changeSeen}>
                                    {isSeen ? <svg xmlns="http://www.w3.org/2000/svg" className="mt-2 h-6 w-6 text-sec" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z" />
                                    </svg> : <svg xmlns="http://www.w3.org/2000/svg" className="mt-2 h-6 w-6 text-gray-100 flex flex-row items-end" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z" />
                                    </svg>}
                                </button>
                                
                            </div>
                        </div>
                        <div className = 'flex flex-col items-center w-full'>
                            {data?.images?.map((x, i ) => {
                                return (<ImageLoader key = {i} props= {{ backEnd: props.backEnd, img: x}}></ImageLoader>)
                            })}
                        </div>
                        <div className = 'flex flex-col items-center my-2'>
                            <div className = "flex flex-row items-center space-x-3 my-2 ">
                                <Link className = {`${data?.prev ? 'flex' : "hidden"} items-center py-1 px-3 bg-sec rounded`} to={data?.prev?.formatedRoutes?.mangaSekaichapterArUrl}>
                                    <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 mx-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M10 19l-7-7m0 0l7-7m-7 7h18" />
                                    </svg>
                                    Back
                                </Link>
                                <Link className = {`${data?.next ? 'flex' : "hidden"} items-center py-1 px-3 bg-sec rounded`} to={data?.next?.formatedRoutes?.mangaSekaichapterArUrl}>
                                    Next
                                    <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 mx-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M14 5l7 7m0 0l-7 7m7-7H3" />
                                    </svg>
                                </Link>
                            </div>
                            <Link className = 'text-white text-2xl border-t-2 border-gray-600 pt-2' to ={data.moreData.formatedRoutes.mangaSekaiUrl}>{data.chapterHeading}</Link>

                        </div>
                    </div>}
        </div>
    )
}

export default MangaReader
