import React, { useEffect, useState } from 'react'
import Navbar from '../components/Navbar'
import axios from 'axios';
import Loader from "react-loader-spinner";
import { useHistory, useParams } from 'react-router-dom';

function Login(inputs) {
    let { props } = inputs;
    let { id } = useParams()

    useEffect(() => {
        setLoading(true)
        if (id) {
            axios({
                url: `${props.backEnd}/account/profile`,
                method: 'POST',
                data: {
                    id: id
                }
            }).then(res => {
                setLoading(false)

                setData(res.data)
            })
        }
    }, [id, props])

    const [loading, setLoading] = useState(true);
    const [data, setData] = useState([]);
    let history = useHistory()

    function handelMyList() {
        history.push('/profile/'+data.id+'/list')
    }


    return (
        <div className ="animate-fadein">
            <Navbar props={ {title: 'Profile', backEnd: props.backEnd}} />
            {loading ? <Loader
                        as='div'
                        className= "flex flex-col items-center mt-[35vh]"
                        type="TailSpin"
                        color="#DCDCDC"
                        height={60}
                        width={60}
                        timeout={60 * 1000} //3 secs
                    /> :
            <div className =" animate-fadeinslow flex flex-col items-center justify-between mt-20">
                <div className ="flex flex-col items-center justify-between pb-2 border-b-2 border-sec">
                    <img src={data.image} className = "h-[10rem] w-[10rem] rounded-full" alt="" />
                    <h1 className = "text-3xl mt-4 text-white flex items-center"><span className ="text-sec mr-2 text-[1.2rem]">{data.role || "Guest"} | </span> {data.username}</h1>
                    <h1 className = "text-xs mt-1 text-gray-200">@{data.id}</h1>
                    <div className ="mt-2 flex items-center space-x-2">
                        <div className ="flex items-center">
                            <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 text-gray-100 mr-1" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
                            </svg>
                            <span className="text-gray-100 text-sm">{data.joiendAt}</span>
                        </div>
                        <div className ="flex items-center">
                            <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 text-gray-100 mr-1" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z" />
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M15 11a3 3 0 11-6 0 3 3 0 016 0z" />
                            </svg>
                            <span className="text-gray-100 text-sm">{data.location}</span>
                        </div>
                    </div>
                    <div className = 'max-w-[85vw] pt-2 border-t-2 border-sec mt-2 w-full'>
                        <div className="mx-2">
                            <h1 className = "text-white text-center -mt-1">About Me</h1>
                            <p className = {`text-white text-center text-sm break-all`}> {data?.aboutUser?.split?.(/(?:\r\n|\r|\n)/g)?.map((el) => {
                                return (<span>{el}<br/></span>)
                            })}</p>
                        </div>
                        
                    </div>
                </div>
                

                <div className ="flex flex-col justify-between mt-4">
                    <span className="text-gray-100 text-xl text-center">{data.username}'s Stats</span>
                    <div className = "bg-thr mt-2 ">
                        <div className ="flex flex-col space-y-1 border-b-2 p-2 border-sec">
                            <span className="text-gray-100 text-sm text-left w-60">Favorite : {data.totalFaveManga}</span>
                            <span className="text-gray-100 text-sm text-left w-60 ">Watching : {data.totalWatchingManga}</span>
                            <span className="text-gray-100 text-sm text-left w-60 ">Plan To Watch : {data.totalPlanToWatchManga}</span>
                            <span className="text-gray-100 text-sm text-left w-60 ">Dropped : {data.totalDroppedManga}</span>
                            <span className="text-gray-100 text-sm text-left w-60 ">Completed : {data.totalCompletedManga}</span>
                        </div>
                        <div className ="flex flex-col space-y-1 border-b-2 p-2 border-sec">
                            <span className="text-gray-100 text-sm w-60 text-center">Total Watched Chapters : {data.totalWatchedChapters}</span>
                          
                        </div>
                    </div>
                </div>
                <button onClick = {() => handelMyList()} className = {`mt-4 transition duration-500 hover:bg-sec flex items-center text-center border-2 border-sec rounded pl-1 pr-2 py-1`}>
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-7 w-6 text-gray-100 mr-1" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9 5H7a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2m-3 7h3m-3 4h3m-6-4h.01M9 16h.01" />
                    </svg>
                    <span className ="text-gray-200 text-center">Show {data.username}'s Lists</span>
                </button>
            </div>
        }
        </div>
    )
}

export default Login
